.sidebar {
  background-color: $lightgray;
  width: $sidebarWidth;
  min-height: calc(100vh - $header-height);

  @include mq('tablet') {
    position: fixed;
    left: 0;
    transform: translateX(-100%);
  }
}
.sidebar__link {
  border-left: 3px solid transparent;
  width: calc(100% - 3px);
  display: flex;
  justify-content: center;
  padding: 3px 0;
  text-decoration: none;
  color: inherit;

  &.active,
  &:hover {
    background-color: $purple6;
    border-left: 3px solid $purple0;

    .material-icons {
      color: $purple0;
    }
  }
}
.sidebar__link + .sidebar__link {
  margin-top: 10px;
}
.sidebar__link:first-child {
  margin-top: 25px;
}
