@mixin solid-button {
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  font-weight: bold;
  @include rem(font-size, 14px);
  line-height: 19px;
  text-transform: capitalize;
  padding: 10px;
}
.primary-button {
  @include solid-button;
  background-color: $purple1;
}

.error-button {
  @include solid-button;
  background-color: $red;
}

.secondary-button {
  background-color: white;
  border: 1px solid $gray;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  color: black;
  cursor: pointer;
  font-weight: bold;
  @include rem(font-size, 14px);
  line-height: 19px;
  text-transform: capitalize;
  padding: 10px;
}

.text-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
